.header {
    width: 100%;
    background: #FCFBFB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 0px;
    padding: 12px 64px;
    position: relative;
  }

  .header a {
    text-decoration: none;
    color: inherit;
  }

  .header_title {
    max-width: 212px;
    width: 100%;
    font-weight: 600;
    font-size: 26px;
    line-height: 36px;
  }